<template lang="pug">
  div.register-container
    header.header-top( @click="$router.go(-1)" )
      span.iconfont.icon-fanhui 
      span 返回
    div.main
      .main-content
        h1.register-success.register-h1  
          span.iconfont.icon-zhucechenggong  
          span.text  注册成功 
        h2.account-label 你的QQ号为:
        h3.you-account {{account}}
      .main-content
        button.login-btn( @click="$router.push({name: 'login'})") 登陆
      .login-notice 
        span 三天内未登录, 该QQ号将被回收
</template>
<script>
import { mapState } from 'vuex'
export default {
  computed: mapState('register', ['account'])
}
</script>
<style lang="stylus" scoped>
  .register-container 
    .header-top 
      padding 1.7rem .5rem 0 .5rem 
      color #737373
    .main  
      padding-top 1.5rem 
    .main-content 
      padding 0 1.6rem
    .register-success
      font-size 2rem
      .iconfont 
        color #8ad03a
      .text 
        color #000 
    .account-label
      font-size 1.3rem
      padding-top 3.1rem
      color #737373 
    .you-account
      padding-top 1.1rem
      font-size 1.8rem
    .login-btn  
      width 100% 
      padding .75rem 0 
      background-color #12b8f6 
      border none 
      border-radius 3px 
      margin-top 2.4rem 
      color #ffffff
      font-size 1rem
    .login-notice  
      padding-top .9rem 
      color  #737373 
      text-align center
      



      
</style>




